import { useEffect, useState, useContext } from 'react'
import { setIsOpenSale } from 'pages/Purchase/store/reducer'
import getTimePeriods from 'utils/getTimePeriods'
import { OpenAtTimestamp } from 'config/constant'
import { useAppDispatch } from 'state'
import { setOpenSaleLayout } from 'state/reducers/app'
import { PurchaseContext } from '../../Purchase/context/PurchaseContext'

let timeoutId = null

const useGetCountDownInSeconds = () => {
  const numberSeconds = OpenAtTimestamp - Math.floor(Date.now() / 1000)
  const [countdownInSeconds, setCoundownInSeconds] = useState(numberSeconds)
  const [timeUntil, setTimeUntil] = useState(getTimePeriods(countdownInSeconds))
  const dispatch = useAppDispatch()
  const { state, purchaseDispatch } = useContext(PurchaseContext)

  useEffect(() => {
    const countDown = () => {
      timeoutId = setTimeout(() => {
        const secondsLeft = OpenAtTimestamp - Math.floor(Date.now() / 1000)
        const newTimeUntil = getTimePeriods(secondsLeft)
        setCoundownInSeconds(secondsLeft)
        setTimeUntil(newTimeUntil)
        // When the countdown time ends, we will clear timeout and show the purchase component
        if (secondsLeft <= 0) {
          clearTimeout(timeoutId)
          purchaseDispatch(setIsOpenSale(true))
          dispatch(setOpenSaleLayout(true))
        }
        return () => clearTimeout(timeoutId)
      }, 1000)
    }
    countDown()
  }, [countdownInSeconds])

  return timeUntil
}

export default useGetCountDownInSeconds
