import { useEffect, useRef, useState } from 'react'
import styled, { keyframes } from 'styled-components'
import Box from 'components/Box'

const flyAni = keyframes`
  0% {
    transform: scale(1) rotateY(0);
  }

  // 50% {
  //   transform: scale(1.2) rotateY(90deg);
  // }

  30% {
    transform: scale(1.2) rotateY(0);
  }
  
  80% {
    transform: scale(1.2) rotateY(180deg);
  }

  100% {
    transform: scale(1) rotateY(180deg);
  }
`

const FlipCardWrapper = styled(Box)`
  height: 400px;
  width: 300px;
  perspective: 1000px;
  background-color: transparent;
  margin: 75px auto;
  &:hover {
    cursor: pointer;
  }
`
const FlipCardInner = styled(Box)`
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);

  // transform: ${(props) => props.showBack && 'rotateY(180deg)'};
  animation-name: ${({ showBack }) => showBack && flyAni};
  animation-duration: 2s;
  // animation-timing-function: linear;
  animation-fill-mode: forwards;
`

const CardFront = styled(Box)`
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
`

const CardBack = styled(Box)`
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transform: rotateY(180deg);
`
const Video = styled.video`
  width: 300px;
  height: 400px;
  position: absolute;
  top: 0;
  left: 0;
`

const OverlayVideo = styled.video`
  width: 100vw;
  height: 100vh;
`

const Image = styled.img`
  width: 100%;
  height: 100%;
`

const FlipCard = ({ data, overlayVideoRef, currentCard, setFlippedCards, flippedCards }) => {
  const [showBack, setShowBack] = useState(false)
  const videoRef = useRef()
  const { cardImage } = data
  const handleClick = () => {
    setFlippedCards([...flippedCards, currentCard])
    setShowBack(true)
  }

  useEffect(() => {
    if (videoRef.current && showBack) {
      setTimeout(() => {
        videoRef.current.play()
      }, 700)
    }
  }, [videoRef.current, showBack])

  useEffect(() => {
    if (overlayVideoRef.current && showBack) {
      setTimeout(() => {
        overlayVideoRef.current.play()
      }, 800)
    }
  }, [overlayVideoRef.current, showBack])

  return (
    <FlipCardWrapper onClick={handleClick}>
      <FlipCardInner showBack={showBack}>
        <CardFront>
          <Image src="/images/card-back.png" alt="card-back" />
        </CardFront>
        <CardBack>
          <Image src={cardImage} alt="card-front" />
          <Video muted ref={videoRef}>
            <source src="/videos/card-opening-1.webm" type="video/webm" />
          </Video>
        </CardBack>
      </FlipCardInner>
    </FlipCardWrapper>
  )
}

export default FlipCard
