import { useEffect, useState } from 'react'
import axios from 'axios'
import { API_CYBALL } from 'config/constant'
import { toast } from 'react-toastify'

const initialState = {
  version: null,
  type: null,
  cardImage: null,
  characterImage: null,
  traitTypes: [],
  traits: [],
  name: null,
  seed: null,
  traitIds: [],
  skill: {
    shooting: null,
    crossing: null,
    physical: null,
    dribbling: null,
    passing: null,
    tackling: null,
    overall: null,
  },
  body: {
    background: null,
    shadow: null,
    right_arm: null,
    body: null,
    legs: null,
    shoes: null,
    pant: null,
    shirt: null,
    left_arm: null,
    head: null,
    mouth: null,
    eyes: null,
    face_accessory: null,
    hair: null,
    ear_piece: null,
    flag: null,
  },
}

const useFetchNftDetail = (tokenId) => {
  const [nftDetail, setNftDetail] = useState(initialState)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const fetchData = async () => {
      try {
        setIsLoading(true)
        const { data } = await axios.get(`${API_CYBALL}/NFTDetails`, {
          headers: {
            'x-functions-key': process.env.REACT_APP_AZURE_CODE,
          },
          params: {
            tokenid: tokenId,
          },
        })
        if (data) {
          setNftDetail(data)
        }
        setIsLoading(false)
      } catch (error) {
        toast.error('Fail to fetch NFT information', {
          hideProgressBar: true,
        })
        setIsLoading(false)
      }
    }

    fetchData()
  }, [])

  return [nftDetail, isLoading]
}

export default useFetchNftDetail
