import React from 'react'
import { HashRouter, Route, Switch, withRouter, Redirect } from 'react-router-dom'
import { QueryParamProvider } from 'use-query-params'
import Menu from 'Layout/Menu'
// import Home from 'pages/Home'
// import Countdown from 'pages/Countdown'
import Purchase from 'pages/Purchase'
import Inventory from 'pages/Inventory'
import AuthRoute from 'utils/AuthRoute'
import useEagerConnect from 'hooks/useEagerConnect'
import useGetAddressData from 'hooks/useGetAddressData'
import { useSelector } from 'react-redux'
import { selectIsLoading } from 'state/reducers/app'

const LoadingScreen = () => {
  return (
    <div className="loadingScreen">
      <div className="lds-roller">
        <div />
        <div />
        <div />
        <div />
        <div />
        <div />
        <div />
        <div />
      </div>
    </div>
  )
}

const AnimatedSwitch = withRouter(({ location }) => (
  <Switch location={location}>
    {/* <Route exact strict path="/">
      <Redirect to="/purchase" />
    </Route> */}
    <Route exact strict path="/purchase">
      <Purchase />
    </Route>
    <Route path="/stash">
      <AuthRoute component={Inventory} />
    </Route>
    <Redirect to="/purchase" />
  </Switch>
))

function App() {
  useEagerConnect()
  useGetAddressData()

  const isLoadingContractAddress = useSelector(selectIsLoading)
  if (isLoadingContractAddress) {
    return <LoadingScreen />
  }
  return (
    <HashRouter>
      <QueryParamProvider ReactRouterRoute={Route}>
        <Menu>
          <AnimatedSwitch />
        </Menu>
      </QueryParamProvider>
    </HashRouter>
  )
}

export default App
