import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import { useSelector } from 'react-redux'
import useRefresh from 'hooks/useRefresh'
import { useCyblocUtilContract } from 'hooks/useContract'
import { selectContractAddress } from 'state/reducers/app'
import { filterPackageIndexes } from '../utils/formatPackageCount'

const DEFAULT_DATA = {
  commonPackageData: [],
  rarePackageData: [],
  epicPackageData: [],
}

const useGetInventoryData = () => {
  const { account } = useWeb3React()
  const [data, setData] = useState(DEFAULT_DATA)
  const [isLoading, setIsLoading] = useState(true)
  const { slowRefresh, fastRefresh } = useRefresh()
  const {
    CYBLOC_PACK_SALE_COMMON: { ADDRESS: COMMON_ADDRESS },
    CYBLOC_PACK_SALE_EPIC: { ADDRESS: EPIC_ADDRESS },
    CYBLOC_PACK_SALE_RARE: { ADDRESS: RARE_ADDRESS },
    CYBLOC_PACK: { ADDRESS: PACK_ADDRESS },
    CYBLOC_UTILS: { ADDRESS: UTIL_ADDRESS },
  } = useSelector(selectContractAddress)

  const cyblocUtilContract = useCyblocUtilContract('0xa3c23FAf7144D06DEB2434f0D3435099994C8b05')
  const getNumberOfUserPackage = async () => {
    try {
      const packDataFromContract = await cyblocUtilContract.methods
        .getCyBlocPacks(PACK_ADDRESS, COMMON_ADDRESS, RARE_ADDRESS, EPIC_ADDRESS, account)
        .call()
      const result = filterPackageIndexes(packDataFromContract)
      return result
    } catch (error) {
      console.log(error, 'Cannot fetch package index info')
      return []
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const packsData = await getNumberOfUserPackage()
        const commonPackageData = []
        const rarePackageData = []
        const epicPackageData = []
        packsData.forEach((pack) => {
          if (pack.packType === '1') {
            commonPackageData.push(pack)
          }

          if (pack.packType === '2') {
            rarePackageData.push(pack)
          }

          if (pack.packType === '3') {
            epicPackageData.push(pack)
          }
        })
        setData({
          ...data,
          commonPackageData,
          rarePackageData,
          epicPackageData,
        })
        setIsLoading(false)
      } catch (error) {
        setIsLoading(false)
        console.log(error, 'error')
      }
    }
    if (account) {
      fetchData()
    }
  }, [account, fastRefresh])

  return [data, isLoading]
}

export default useGetInventoryData
