import React, { useMemo, useState } from 'react'
import { CSSTransition } from 'react-transition-group'
import { CardText, SelectedIcon, Wrapper, CardImage } from './styles'

const RarePackageCard = ({ data, selectedPackage, onClick }) => {
  const isSelected = useMemo(() => {
    const { type, packageIndex } = selectedPackage
    if (type === '2' && packageIndex.includes(data)) {
      return true
    }

    return false
  }, [selectedPackage])
  return (
    <Wrapper onClick={onClick}>
      <CardImage src="/images/5-Pack-Rare.png" />
      <CSSTransition in={isSelected} timeout={200} classNames="fade" unmountOnExit appear>
        <SelectedIcon src="/images/selector.png" />
      </CSSTransition>
      <CardText fontSize="2.2rem" mb="10px">
        Rare #{Number(data)}
      </CardText>
    </Wrapper>
  )
}

export default RarePackageCard
