import React, { useState } from 'react'
import styled from 'styled-components'
import { SwitchTransition, CSSTransition } from 'react-transition-group'
import { useSelector } from 'react-redux'
import { selectSelectedPackId } from 'state/reducers/cybloc'
import Flex from 'components/Flex'
import Text from 'components/Text'
import {
  PackageWrapper,
  CardWrapper,
  ItemInfo,
  ItemWrapper,
  StyledClassInfoHeader,
  PreviewText,
  PreviewWrapper,
  Video,
  ProbabilityWrapper,
  StyledClassName,
  StyledClassInput,
  StyledClassPercentage,
  StyledInput,
  StatsText,
} from '../../../Purchase/components/styled'
import theme from '../../../Purchase/components/styled/colors'

const CloseImage = styled.img`
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  color: #f3f0fe;
  width: 40px;
  height: 40px;
`
const PackNumber = styled(Text)`
  font-size: 2.2rem;
  position: absolute;
  top: 40px;
  left: 50px;
  color: #ffffff;

  @media (max-width: 768px) {
    top: 20px;
  }
`

export const PreviewCommon = ({ onDismiss }) => {
  const [isPreview, setIsPreview] = useState(true)
  const packId = useSelector(selectSelectedPackId)
  const { common } = theme

  const Preview = () => {
    return (
      <PreviewWrapper justifyContent="center">
        <Video muted autoPlay loop playsInline>
          <source src="https://cdn-genesis.cyball.com/cyblocpack-images/Common-Preview.mp4" type="video/mp4" />
        </Video>
      </PreviewWrapper>
    )
  }

  const Stats = () => {
    return (
      <ProbabilityWrapper>
        <Text color="#F3F3F3" fontSize="1.6rem" pb="20px">
          Chance to obtain each Class (%)
        </Text>
        <Flex pb="20px" alignItems="center">
          <StyledClassName>Bronze</StyledClassName>
          <StyledClassPercentage>82.37</StyledClassPercentage>
          <StyledClassInput>
            <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={82.37} />
          </StyledClassInput>
        </Flex>
        <Flex pb="20px" alignItems="center">
          <StyledClassName>Silver</StyledClassName>
          <StyledClassPercentage>14.82</StyledClassPercentage>
          <StyledClassInput>
            <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={14.82} />
          </StyledClassInput>
        </Flex>
        <Flex pb="20px" alignItems="center">
          <StyledClassName>Gold</StyledClassName>
          <StyledClassPercentage>2.64</StyledClassPercentage>
          <StyledClassInput>
            <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={2.64} />
          </StyledClassInput>
        </Flex>
        <Flex pb="20px" alignItems="center">
          <StyledClassName>Platinum</StyledClassName>
          <StyledClassPercentage>0.14</StyledClassPercentage>
          <StyledClassInput>
            <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={0.14} />
          </StyledClassInput>
        </Flex>
        <Flex alignItems="center">
          <StyledClassName>Legendary</StyledClassName>
          <StyledClassPercentage>0.03</StyledClassPercentage>
          <StyledClassInput>
            <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={0.03} />
          </StyledClassInput>
        </Flex>
      </ProbabilityWrapper>
    )
  }
  return (
    <PackageWrapper alignItems="center" justifyContent="center" width="100%">
      <PackNumber># Common {Number(packId)}</PackNumber>
      <CloseImage
        src="/images/close.png"
        onClick={() => {
          onDismiss()
        }}
      />
      <ItemWrapper>
        <CardWrapper src={common.cardBackground} />
        <Video muted autoPlay loop playsInline>
          <source src="https://genesis.cyball.com/cyblocpack-media/common.mp4" type="video/mp4" />
        </Video>
      </ItemWrapper>
      <ItemInfo cardBackgroundColor={common.cardBackgroundColor}>
        <StyledClassInfoHeader>
          <PreviewText isPreview={isPreview} onClick={() => setIsPreview(true)}>
            Preview
          </PreviewText>
          <StatsText isPreview={isPreview} onClick={() => setIsPreview(false)}>
            Probability
          </StatsText>
        </StyledClassInfoHeader>
        <SwitchTransition mode="out-in">
          <CSSTransition
            key={isPreview}
            addEndListener={(node, done) => {
              node.addEventListener('transitionend', done, false)
            }}
            classNames="fade"
          >
            {isPreview ? <Preview /> : <Stats />}
          </CSSTransition>
        </SwitchTransition>
      </ItemInfo>
    </PackageWrapper>
  )
}

