import React, { useCallback, useMemo, useState } from 'react'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import { useHistory, useLocation } from 'react-router-dom'
import { useWeb3React } from '@web3-react/core'
import Flex from 'components/Flex'
import Text from 'components/Text'
import Box from 'components/Box'
import { useModal } from 'components/Modal'
import VideoModal from 'components/Modal/VideoModal'
import { urlConfig } from 'config/constant'
import useEthBalance from 'hooks/useEthBalance'
import { selectOpenSaleLayout } from 'state/reducers/app'
import MenuItem from './MenuItem'
import UserBlock from './Userblock'
import BurgerMenu, { StyledBurgerIcon } from './BurgerMenu'

const StyledWrapper = styled(Box)`
  width: 100%;
  height: 90px;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  position: relative;
`
const Redirect = styled.a`
  font-size: 1.7rem;
  color: #3de131;
  font-weight: 700;
  cursor: pointer;
  padding: 20px;
  text-decoration: none;
  transition: opacity 400ms ease;
  opacity: 0.6;

  &:hover {
    opacity: 1;
  }
`

const Row = styled.nav`
  display: flex;
  max-width: 1000px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
  position: relative;
`

const StyledText = styled(Text)`
  font-size: 1.7rem;
  color: #b89ef4;
  cursor: pointer;
  opacity: 0.7;
  font-weight: 700;
  padding: 20px;
  transition: opacity 400ms ease;

  &:hover {
    opacity: 1;
  }
`

const LogoWrapper = styled.img`
  width: 150px;

  &:hover {
    cursor: pointer;
  }
`
const MenuItemWrapper = styled(Flex)`
  width: 100%;

  @media (max-width: 1000px) {
    display: none;
  }
`
const CollapseMenu = ({ urlConfigAccount, open, setOpen, onNavigateTo, onPresentInstructionModal }) => {
  return (
    <>
      <StyledBurgerIcon open={open} onClick={() => setOpen(!open)}>
        <Box />
        <Box />
        <Box />
      </StyledBurgerIcon>
      <BurgerMenu
        urlConfig={urlConfigAccount}
        onNavigateTo={onNavigateTo}
        open={open}
        onPresentInstructionModal={onPresentInstructionModal}
      />
    </>
  )
}

const Header = () => {
  const history = useHistory()
  const { account } = useWeb3React()
  const [onPresentInstructionModal] = useModal(<VideoModal />)
  const [open, setOpen] = useState(false)
  const { pathname } = useLocation()
  const balance = useEthBalance()
  const openSaleLayout = useSelector(selectOpenSaleLayout)
  const onNavigateTo = useCallback((route) => {
    if (route) {
      history.push(route)
      setOpen(false)
    }
  }, [])

  const URL_CONFIG = useMemo(() => {
    // Do not show anything on header when open sale not open
    // if (!openSaleLayout) {
    //   return []
    // }

    if (account) {
      return urlConfig
    }

    return urlConfig.filter((link) => link.name !== 'Stash')
  }, [account, openSaleLayout])

  return (
    <StyledWrapper openSaleLayout={openSaleLayout}>
      <Row>
        <LogoWrapper src="/images/logo.png" onClick={() => onNavigateTo('/')} />
        <MenuItemWrapper justifyContent="flex-end" alignItems="center">
          {URL_CONFIG.map(({ name, path, isRedirect, url }) => {
            const isActive = path === '/' ? pathname === path : pathname.includes(path)
            if (isRedirect) {
              return <Redirect onClick={onPresentInstructionModal}>{name}</Redirect>
            }
            return <MenuItem isActive={isActive} name={name} path={path} onNavigateTo={onNavigateTo} key={name} />
          })}
          <UserBlock balance={balance} />
        </MenuItemWrapper>
        <CollapseMenu
          urlConfigAccount={URL_CONFIG}
          open={open}
          setOpen={setOpen}
          onNavigateTo={onNavigateTo}
          onPresentInstructionModal={onPresentInstructionModal}
        />
      </Row>
    </StyledWrapper>
  )
}

export default Header
