import React from 'react'
import { Switch, Route, useRouteMatch } from 'react-router-dom'
import Stash from './Stash'
import PackageOpening from './PackageOpening'
import NFTDetail from './NFTDetail'
import useGetInventoryData from './hooks/useGetInventoryData'
import useGetCyblocList from './hooks/useGetCyblocList'

const Inventory = () => {
  // useGetCyblocList()
  const { path } = useRouteMatch()
  const [data, isLoading] = useGetInventoryData()
  return (
    <Switch>
      <Route path={`${path}/package-opening/:packageType`}>
        <PackageOpening packageData={data} />
      </Route>
      <Route path={`${path}/nft-detail/:nftId`}>
        <NFTDetail />
      </Route>
      <Route path={`${path}`}>
        <Stash packageData={data} isLoading={isLoading} />
      </Route>
    </Switch>
  )
}

export default Inventory
