export const filterPackageIndexes = (packs) => {
  const packList = []
  if (packs && packs.length > 0) {
    // eslint-disable-next-line array-callback-return
    packs.map((pack) => {
      packList.push({
        packType: pack[0],
        packIndex: pack[1],
        tokenId: pack[2],
      })
    })

    return packList
  }

  return []
}
