import React from 'react'
import styled, { keyframes } from 'styled-components'
import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'

const fadeEffect = keyframes`
  0% { opacity: 0; }
  40% { opacity: 1; }
  60% { opacity: 1; }
  100% {opacity: 0;}
`
const dotElasticAfter = keyframes`
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1, 0.67);
  }
  75% {
    transform: scale(1, 1.5);
  }
  100% {
    transform: scale(1, 1);
  }
`
const dotElasticBefore = keyframes`
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(1, 1.5);
  }
  50% {
    transform: scale(1, 0.67);
  }
  75% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
`

const dotElastic = keyframes`
  0% {
    transform: scale(1, 1);
  }
  25% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1, 1.5);
  }
  75% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
`

const Overlay = styled(Flex)`
  width: 100%;
  height: calc(100vh - 305px);
  z-index: 1;
  position: static;
  top: 90px;
  left: 0;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
`

const LogoWrapper = styled(Box)`
  position: relative;
`

const LoaderIcon = styled.img`
  animation: ${fadeEffect} 1.5s;
  animation-iteration-count: infinite;
  width: 300px;

  @media (max-width: 576px) {
    width: 250px;
  }
`
const SubLoaderIcon = styled.img`
  width: 300px;
  position: absolute;
  left: 0;
  top: 0;

  @media (max-width: 576px) {
    width: 250px;
  }
`

const LoadingText = styled(Text)`
  font-weight: 500;
  font-size: 4rem;
  letter-spacing: 2px;
  color: #f3f3f3;
`

const StyledDot = styled.span`
  position: relative;
  width: 7px;
  height: 7px;
  border-radius: 5px;
  background-color: #ffffff;
  color: #ffffff;
  animation: ${dotElastic} 1s infinite linear;

  &:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: -15px;
    width: 7px;
    height: 7px;
    border-radius: 5px;
    background-color: #ffffff;
    color: #ffffff;
    animation: ${dotElasticBefore} 1s infinite linear;
  }

  &:after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
    left: 15px;
    width: 7px;
    height: 7px;
    border-radius: 5px;
    background-color: #ffffff;
    color: #ffffff;
    animation: ${dotElasticAfter} 1s infinite linear;
  }
`

const PageLoading = () => (
  <Overlay>
    <LogoWrapper>
      <LoaderIcon src="/images/loading_logo.png" />
      <SubLoaderIcon src="/images/loading_logo.png" />
    </LogoWrapper>
    <Flex alignItems="flex-end">
      <LoadingText mr="24px">Loading</LoadingText>
      <StyledDot />
    </Flex>
  </Overlay>
)

export default PageLoading
