import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { buyCyblocPack } from 'utils/callHelpers'
import { useSelector } from 'react-redux'
import { selectPackProof } from 'state/reducers/app'
import { useCyblockPresaleContract } from './useContract'

// We will select correct contract, based on selected package
const useBuyCyblocsPacks = (packType) => {
  const { account } = useWeb3React()
  const { common, rare, epic } = useSelector(selectPackProof)
  let config = null
  if (packType === 'common') {
    config = common
  } else if (packType === 'rare') {
    config = rare
  } else {
    config = epic
  }
  const { contract, proof, maxBuy } = config
  const cyblockPresaleContract = useCyblockPresaleContract(contract)
  const handleBuyCyblockPack = useCallback(
    async (quantity, amount) => {
      const txHash = await buyCyblocPack(cyblockPresaleContract, quantity, amount, account, proof, maxBuy)
      return txHash
    },
    [account, cyblockPresaleContract],
  )

  return { onBuyPack: handleBuyCyblockPack }
}

export default useBuyCyblocsPacks
