import React, { useEffect, useState } from 'react'
import axios from 'axios'
import get from 'lodash/get'
import { API_CYBALL, PACKAGE_TYPE, CAMPAIGN_TYPE } from 'config/constant'
import { setContractAddress, setPackProof, setIsLoading } from 'state/reducers/app'
import { useAppDispatch } from 'state'
import { useWeb3React } from '@web3-react/core'

const useGetAddressData = () => {
  const dispatch = useAppDispatch()
  const { account } = useWeb3React()
  const [isLoadingProof, setIsLoadingProof] = useState(false)
  const [isLoadingConfig, setIsLoadingConfig] = useState(true)

  useEffect(() => {
    const fetchBuyProofData = async () => {
      try {
        setIsLoadingProof(true)
        const PROOFS_PROMISE_LIST = []
        // eslint-disable-next-line array-callback-return
        PACKAGE_TYPE.map((pType) => {
          const promise = axios.get(`${API_CYBALL}/buy-proof`, {
            params: {
              buyer: account,
              campaign: CAMPAIGN_TYPE.GENESIS,
              type: pType,
            },
          })
          PROOFS_PROMISE_LIST.push(promise)
        })

        const result = await Promise.all(PROOFS_PROMISE_LIST)
        if (result) {
          const filteredResult = result.map((r) => {
            return { ...get(r, 'data', {}) }
          })
          dispatch(
            setPackProof({
              common: filteredResult[0],
              rare: filteredResult[1],
              epic: filteredResult[2],
            }),
          )
        }
        setIsLoadingProof(false)
      } catch (error) {
        console.log(error, 'Fail to load proof')
        setIsLoadingProof(false)
      }
    }
    if (account) {
      fetchBuyProofData()
    }
  }, [account])

  useEffect(() => {
    const fetchContractAddress = async () => {
      try {
        dispatch(setIsLoading(true))
        const response = await axios.get(`${API_CYBALL}/config`)
        const contractAddress = get(response, 'data.contractConfig', {})
        dispatch(setContractAddress(contractAddress))
        setIsLoadingConfig(false)
      } catch (error) {
        setIsLoadingConfig(false)
        console.log(error, 'Fail to fetch contract info')
      }
    }

    fetchContractAddress()
  }, [])

  useEffect(() => {
    if (!isLoadingConfig && !isLoadingProof) {
      dispatch(setIsLoading(false))
    }
  }, [isLoadingConfig, isLoadingProof])

  return null
}

export default useGetAddressData
