import React, { useContext, useState, useMemo, useEffect } from 'react'
import { toast } from 'react-toastify'
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import BigNumber from 'bignumber.js'
import Box from 'components/Box'
import Text from 'components/Text'
import Flex from 'components/Flex'
import SliderComponent from 'components/Slider'
import useBuyCyblocsPacks from 'hooks/useBuyCyblocsPacks'
import useMediaQuery from 'hooks/useMediaQuery'
import Countdown from 'pages/Countdown'
import { useAppDispatch } from 'state'
import { selectContractAddress } from 'state/reducers/app'
import { setNewCylocList } from 'state/reducers/cybloc'
import contractConfig from 'config/constant/contract'
import { packageConfig } from 'config/constant'
import { PurchaseContext } from './context/PurchaseContext'
import { setSelectedPackage, changeNumberOfPackage, setLoading } from './store/reducer'
import PackageCommon from './components/PackageCommon'
import PackageRare from './components/PackageRare'
import PackageEpic from './components/PackageEpic'

const Wrapper = styled(Flex)`
  margin: 0 auto;
  position: relative;
  max-width: 1000px;
  padding: 45px 0px;

  @media (max-width: 768px) {
    padding-bottom: 0px;
  }
`
const SliderWrapper = styled(Box)`
  max-width: 100%;
  position: relative;
  z-index: 10;
  margin: 0 auto;
  padding: 0 100px;

  @media (max-width: 576px) {
    padding: 0px;
  }
`
const StyledText = styled(Text)`
  width: 33%;
  text-align: center;
  font-size: 3rem;
  cursor: pointer;
  @media (max-width: 576px) {
    font-size: 2rem;
    font-weight: 700;
  }
`
const UnionImage = styled.img`
  width: 350px;
  bottom: -10px;
  position: absolute;

  @media (max-width: 780px) {
    width: 200px;
    bottom: 100px;
  }

  @media (max-width: 576px) {
    display: none;
  }
`
const UnionImage2 = styled.img`
  position: absolute;
  width: 350px;
  bottom: 90px;
  left: 375px;

  @media (max-width: 780px) {
    width: 200px;
    bottom: 200px;
  }

  @media (max-width: 576px) {
    display: none;
  }
`
const UnionImage3 = styled.img`
  width: 350px;
  bottom: -10px;
  position: absolute;
  right: 0px;

  @media (max-width: 780px) {
    bottom: 100px;
    width: 200px;
  }

  @media (max-width: 576px) {
    display: none;
  }
`
const Package = () => {
  const { state, purchaseDispatch } = useContext(PurchaseContext)
  const { account } = useWeb3React()
  const [index, setIndex] = useState(0)
  const [nav1, setNav1] = useState()
  const dispatch = useAppDispatch()
  const [nav2, setNav2] = useState()
  const { selectedPackage, numberOfPackage, isLoading, isOpenSale } = state
  const isMobile = useMediaQuery('(max-width: 576px)')
  const {
    CYBLOC_PACK_SALE_COMMON: { ADDRESS: COMMON_ADDRESS, PRICE: COMMON_PRICE },
    CYBLOC_PACK_SALE_EPIC: { ADDRESS: EPIC_ADDRESS, PRICE: EPIC_PRICE },
    CYBLOC_PACK_SALE_RARE: { ADDRESS: RARE_ADDRESS, PRICE: RARE_PRICE },
  } = useSelector(selectContractAddress)
  const { onBuyPack: onBuyCommonPackage } = useBuyCyblocsPacks('common')
  const { onBuyPack: onBuyRarePackage } = useBuyCyblocsPacks('rare')
  const { onBuyPack: onBuyEpicPackage } = useBuyCyblocsPacks('epic')
  useEffect(() => {
    dispatch(setNewCylocList([]))
  }, [])

  const payAmount = useMemo(() => {
    if (selectedPackage === 'common' && COMMON_PRICE) {
      return new BigNumber(numberOfPackage).multipliedBy(new BigNumber(COMMON_PRICE)).toString()
    }

    if (selectedPackage === 'rare' && RARE_PRICE) {
      return new BigNumber(numberOfPackage).multipliedBy(new BigNumber(RARE_PRICE)).toString()
    }

    if (selectedPackage === 'epic' && EPIC_PRICE) {
      return new BigNumber(numberOfPackage).multipliedBy(new BigNumber(EPIC_PRICE)).toString()
    }

    return 0
  }, [numberOfPackage, COMMON_PRICE, RARE_PRICE, EPIC_PRICE, selectedPackage])

  // Function to change number of package which user want to buy
  const onChangeValue = (status) => {
    if (status === 'up') {
      purchaseDispatch(changeNumberOfPackage(numberOfPackage + 1))
    }

    if (status === 'down' && numberOfPackage > 1) {
      purchaseDispatch(changeNumberOfPackage(numberOfPackage - 1))
    }
  }

  const onHandleChangeSlide = (i) => {
    if (nav2) {
      nav2.slickGoTo(i)
    }
  }

  const onChangeSlider = (current, next) => {
    setIndex(next)
    purchaseDispatch(changeNumberOfPackage(1))
    if (next === 0) {
      purchaseDispatch(setSelectedPackage('common'))
    }

    if (next === 1) {
      purchaseDispatch(setSelectedPackage('rare'))
    }

    if (next === 2) {
      purchaseDispatch(setSelectedPackage('epic'))
    }
  }

  const onHandleBuyPack = async () => {
    try {
      let txStatus = null
      if (!account) {
        toast.error('Log in before purchasing', {
          hideProgressBar: true,
        })
        return
      }

      if (selectedPackage === 'common') {
        const { status } = await onBuyCommonPackage(String(numberOfPackage), payAmount)
        txStatus = status
      } else if (selectedPackage === 'rare') {
        const { status } = await onBuyRarePackage(String(numberOfPackage), payAmount)
        txStatus = status
      } else {
        const { status } = await onBuyEpicPackage(String(numberOfPackage), payAmount)
        txStatus = status
      }
      if (txStatus) {
        toast.success('Purchase successfully', {
          hideProgressBar: true,
        })
      }
    } catch (error) {
      console.log(error, 'error ?')
      if (error && error.code === 4001) {
        return
      }
      toast.error('Fail to buy package', {
        hideProgressBar: true,
      })
    }
  }

  // if (!isOpenSale) {
  //   return (
  //     <CountdownWrapper url="/images/background_countdown.png">
  //       <Countdown />
  //     </CountdownWrapper>
  //   )
  // }

  if (isMobile) {
    return (
      <Wrapper flexDirection="column" justifyContent="space-between">
        <Flex width="100%">
          <SliderWrapper>
            <SliderComponent
              asNavFor={nav1}
              ref={(slider2) => setNav2(slider2)}
              slidesToShow={1}
              slidesToScroll={1}
              infinite
              fade
              draggable={false}
              swipe={false}
              speed={500}
              dots={false}
              arrows={false}
            >
              <Box>
                <Flex pb="30px">
                  <StyledText color="#D3D3D3" onClick={() => onHandleChangeSlide(0)}>
                    Common
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                    Rare
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                    Epic
                  </StyledText>
                </Flex>
              </Box>
              <Box>
                <Flex pb="30px">
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                    Common
                  </StyledText>
                  <StyledText color="#F2C552" onClick={() => onHandleChangeSlide(1)}>
                    Rare
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                    Epic
                  </StyledText>
                </Flex>
              </Box>
              <Box>
                <Flex pb="30px">
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                    Common
                  </StyledText>
                  <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                    Rare
                  </StyledText>
                  <StyledText color="#9163FF" onClick={() => onHandleChangeSlide(2)}>
                    Epic
                  </StyledText>
                </Flex>
              </Box>
            </SliderComponent>
          </SliderWrapper>
        </Flex>
        <SliderWrapper>
          <SliderComponent
            asNavFor={nav2}
            ref={(slider1) => setNav1(slider1)}
            index={index}
            loading={isLoading}
            beforeChange={onChangeSlider}
          >
            <PackageCommon onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
            <PackageRare onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
            <PackageEpic onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
          </SliderComponent>
        </SliderWrapper>
      </Wrapper>
    )
  }

  return (
    <Wrapper flexDirection="column" justifyContent="space-between">
      <Flex width="100%">
        <SliderWrapper>
          <SliderComponent
            asNavFor={nav1}
            ref={(slider2) => setNav2(slider2)}
            slidesToShow={1}
            slidesToScroll={1}
            infinite
            fade
            draggable={false}
            swipe={false}
            speed={500}
            dots={false}
            arrows={false}
          >
            <Box>
              <Flex pt="60px" pb="50px">
                <StyledText color="#D3D3D3" onClick={() => onHandleChangeSlide(0)}>
                  Common
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                  Rare
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                  Epic
                </StyledText>
              </Flex>
            </Box>
            <Box>
              <Flex pt="60px" pb="50px">
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                  Common
                </StyledText>
                <StyledText color="#F2C552" onClick={() => onHandleChangeSlide(1)}>
                  Rare
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(2)}>
                  Epic
                </StyledText>
              </Flex>
            </Box>
            <Box>
              <Flex pt="60px" pb="50px">
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(0)}>
                  Common
                </StyledText>
                <StyledText color="#f3f3f3" opacity="0.5" onClick={() => onHandleChangeSlide(1)}>
                  Rare
                </StyledText>
                <StyledText color="#9163FF" onClick={() => onHandleChangeSlide(2)}>
                  Epic
                </StyledText>
              </Flex>
            </Box>
          </SliderComponent>
        </SliderWrapper>
      </Flex>
      <SliderWrapper>
        <SliderComponent
          asNavFor={nav2}
          ref={(slider1) => setNav1(slider1)}
          index={index}
          loading={isLoading}
          beforeChange={onChangeSlider}
        >
          <PackageCommon onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
          <PackageRare onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
          <PackageEpic onChangeValue={onChangeValue} onHandleBuyPack={onHandleBuyPack} payAmount={payAmount} />
        </SliderComponent>
      </SliderWrapper>
      <UnionImage src="/images/Union.svg" />
      <UnionImage2 src="/images/Union.svg" />
      <UnionImage3 src="/images/Union.svg" />
    </Wrapper>
  )
}

export default Package
