import { useEffect, useState } from 'react'
import useRefresh from 'hooks/useRefresh'
import { useCyblockPresaleContract } from './useContract'

const DEFAULT_DATA = {
  numberOfSoldPacks: null,
  totalPacks: null,
}

const useGetPackSaleInfo = (contractAddress) => {
  const [data, setData] = useState(DEFAULT_DATA)
  const [isLoading, setIsLoading] = useState(true)
  const { slowRefresh, fastRefresh } = useRefresh()
  const packageContract = useCyblockPresaleContract(contractAddress)

  const getUserPackCount = async () => {
    try {
      const numberOfSoldPacks = await packageContract.methods.numberOfSoldPack().call()
      const totalPack = await packageContract.methods.TOTAL_PACK().call()

      return [numberOfSoldPacks, totalPack]
    } catch (error) {
      console.log(error, 'Cannot fetch package index info')
      return null
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [numberOfSoldPacks, totalPacks] = await getUserPackCount()
        setData({
          numberOfSoldPacks,
          totalPacks,
        })
        setIsLoading(false)
      } catch (error) {
        setIsLoading(false)
        console.log(error, 'error')
      }
    }
    if (contractAddress) {
      fetchData()
    }
  }, [fastRefresh])

  return [data, isLoading]
}

export default useGetPackSaleInfo
