/* eslint-disable prefer-destructuring */
import React, { useState, useEffect, useMemo, useRef } from 'react'
import { useSelector } from 'react-redux'
import queryString from 'query-string'
import { useParams, useLocation } from 'react-router-dom'
import { animated, useTransition, useSpringRef } from 'react-spring'
import { useModal, CardDetailModal } from 'components/Modal'
import SliderComponent from 'components/Slider'
import { selectNewCyblocList, setSelectedNftId } from 'state/reducers/cybloc'
import { useAppDispatch } from 'state'
import { CommonOpeningCard, RareOpeningCard, EpicOpeningCard } from '../components/OpeningCard'
import {
  Row,
  Wrapper,
  OverlayVideo,
  TransitionWrapper,
  UnionImage,
  UnionImage2,
  CardText,
  SliderWrapper,
} from './styles'
import ActionButton from './ActionButton'
import Deck from './Deck'
import FlipCard from './FlipCard'
import NFTDetail from '../NFTDetail'

const PackageOpening = () => {
  const [pages, setPages] = useState([])
  const [flippedCards, setFlippedCards] = useState([])
  const [step, setStep] = useState(1)
  const [index, setIndex] = useState(0)
  const { packageType } = useParams()
  const overlayVideoRef = useRef()
  const location = useLocation()
  const dispatch = useAppDispatch()
  const newPackages = useSelector(selectNewCyblocList)
  const packageIndexes = useMemo(() => {
    const queriesObj = queryString.parse(location.search, { arrayFormat: 'bracket' })
    if (queriesObj.packageIndex) {
      return queriesObj.packageIndex
    }

    return []
  }, [location.search])
  const [onPresentViewCyBlocDetailModal, onDimiss] = useModal(
    <CardDetailModal>
      <NFTDetail
        onDismiss={() => {
          onDimiss()
        }}
      />
    </CardDetailModal>,
  )

  useEffect(() => {
    const CARD_LIST = [1].map(() => {
      if (packageType === '1') {
        return <CommonOpeningCard />
      }

      if (packageType === '2') {
        return <RareOpeningCard />
      }

      return <EpicOpeningCard />
    })

    setPages([
      ({ style }) => (
        <animated.div style={{ ...style }}>
          <Deck
            cards={CARD_LIST}
            isDraggable={false}
            isStacked
            cardContainerStyle={{
              top: '0px',
            }}
          />
        </animated.div>
      ),
    ])
  }, [])

  const transRef = useSpringRef()
  /* Config for transition */
  const transitions = useTransition(pages, {
    ref: transRef,
    keys: null,
    from: { opacity: 0, transform: 'translateY(-100px)' },
    enter: (item) => async (next, cancel) => {
      await next({ opacity: 1, position: 'relative', zIndex: 2 })
      await next({ transform: 'translateY(0px)' })
    },
    leave: (item) => async (next, cancel) => {
      await next({ opacity: 0, transform: 'translateY(-100px)' })
    },
    config: { duration: 500 },
    expires: true,
    trail: 2000,
  })

  useEffect(() => {
    transRef.start()
  }, [pages])

  return (
    <Wrapper>
      <OverlayVideo muted ref={overlayVideoRef}>
        <source src="/videos/card-opening-2.webm" type="video/webm" />
      </OverlayVideo>
      <Row>
        <UnionImage src="/images/Union.svg" />
        <UnionImage2 src="/images/Union.svg" />
        <TransitionWrapper>
          {transitions((style, Page) => {
            return (
              <animated.div style={{ ...style }}>
                <Page packageType={packageType} />
              </animated.div>
            )
          })}
          {step === 2 && (
            <CardText fontSize="2.6rem">
              Cybloc NFT {index + 1}/{newPackages.length}
            </CardText>
          )}
        </TransitionWrapper>
        <ActionButton
          packageIndexes={packageIndexes}
          flippedCards={flippedCards}
          currentCard={index}
          onViewCardDetail={() => {
            const { tokenId } = newPackages[index]
            dispatch(setSelectedNftId(tokenId))
            onPresentViewCyBlocDetailModal()
          }}
          onClick={() => {
            setPages([
              ({ style }) => (
                <animated.div style={{ ...style }}>
                  <SliderWrapper>
                    <SliderComponent index={index} beforeChange={(_, next) => setIndex(next)}>
                      {newPackages.map((p, i) => {
                        return (
                          <FlipCard
                            data={p}
                            overlayVideoRef={overlayVideoRef}
                            currentCard={i}
                            setFlippedCards={setFlippedCards}
                            flippedCards={flippedCards}
                            key={flippedCards}
                          />
                        )
                      })}
                    </SliderComponent>
                  </SliderWrapper>
                </animated.div>
              ),
            ])
            setStep(2)
          }}
          step={step}
        />
      </Row>
    </Wrapper>
  )
}

export default PackageOpening
