import React, { useMemo } from 'react'
import { CSSTransition } from 'react-transition-group'
import { Wrapper, CardImage, CardText, SelectedIcon } from './styles'

const CommonPackageCard = ({ data, selectedPackage, onClick }) => {
  const isSelected = useMemo(() => {
    const { type, packageIndex } = selectedPackage
    if (type === '1' && packageIndex.includes(data)) {
      return true
    }

    return false
  }, [selectedPackage])

  return (
    <Wrapper onClick={onClick}>
      <CardImage src="/images/5-Pack-Common.png" />
      <CSSTransition in={isSelected} timeout={200} classNames="fade" unmountOnExit appear>
        <SelectedIcon src="/images/selector.png" />
      </CSSTransition>
      <CardText fontSize="2.2rem" mb="10px">
        Common #{Number(data)}
      </CardText>
    </Wrapper>
  )
}

export default CommonPackageCard
