import React, { useContext, useState, useMemo } from 'react'
import { CSSTransition, SwitchTransition } from 'react-transition-group'
import { useSelector } from 'react-redux'
import get from 'lodash/get'
import { selectContractAddress, selectPackProof } from 'state/reducers/app'
import Text from 'components/Text'
import Flex from 'components/Flex'
import LoaderIcon from 'components/LoaderIcon'
import useGetPackSaleInfo from 'hooks/useGetPackSaleInfo'
import { getFullDisplayBalance } from 'utils/formatBalance'
import { PurchaseContext } from '../../context/PurchaseContext'
import {
  StatsText,
  StyledChangeValueButton,
  StyledClassName,
  StyledClassPercentage,
  StyledClassInput,
  StyledInput,
  PreviewImage,
  PackageWrapper,
  PriceText,
  CardWrapper,
  ItemInfo,
  ItemWrapper,
  TextWrapper,
  StyledButton,
  StyledNumberOfPackage,
  StyledClassInfoHeader,
  PreviewText,
  ProbabilityWrapper,
  PreviewWrapper,
  Video,
  StyledNumberOfSoldPackage,
} from '../styled'
import theme from '../styled/colors'

const Stats = () => {
  const { common } = theme

  return (
    <ProbabilityWrapper>
      <Text color="#F3F3F3" fontSize="1.6rem" pb="20px">
        Chance to obtain each Class (%)
      </Text>
      <Flex pb="20px" alignItems="center">
        <StyledClassName>Bronze</StyledClassName>
        <StyledClassPercentage>82.37</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={82.37} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px" alignItems="center">
        <StyledClassName>Silver</StyledClassName>
        <StyledClassPercentage>14.82</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={14.82} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px" alignItems="center">
        <StyledClassName>Gold</StyledClassName>
        <StyledClassPercentage>2.64</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={2.64} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px" alignItems="center">
        <StyledClassName>Platinum</StyledClassName>
        <StyledClassPercentage>0.14</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={0.14} />
        </StyledClassInput>
      </Flex>
      <Flex alignItems="center">
        <StyledClassName>Legendary</StyledClassName>
        <StyledClassPercentage>0.03</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={0.03} />
        </StyledClassInput>
      </Flex>
    </ProbabilityWrapper>
  )
}

const Preview = () => {
  return (
    <PreviewWrapper justifyContent="center">
      <Video muted autoPlay loop playsInline poster="/images/thumbnail-common-preview.png">
        <source src="https://cdn-genesis.cyball.com/cyblocpack-images/Common-Preview.mp4" type="video/mp4" />
      </Video>
    </PreviewWrapper>
  )
}

const PackageCommon = ({ onChangeValue, onHandleBuyPack, payAmount }) => {
  const { state } = useContext(PurchaseContext)
  const [isPreview, setIsPreview] = useState(true)
  const [isLoading, setIsLoading] = useState(false)
  const { common: commonPackConfig } = useSelector(selectPackProof)
  const { CYBLOC_PACK_SALE_COMMON } = useSelector(selectContractAddress)
  const [{ numberOfSoldPacks, totalPacks }, isLoadingSaleInfo] = useGetPackSaleInfo(CYBLOC_PACK_SALE_COMMON.ADDRESS)
  const { numberOfPackage } = state
  const { common } = theme
  const limitBoughtPackage = useMemo(() => {
    return get(commonPackConfig, 'maxBuy', null)
  }, [commonPackConfig])

  const currentBuy = useMemo(() => {
    return get(commonPackConfig, 'currentAmount', null)
  }, [commonPackConfig])

  const onBuyPack = async () => {
    setIsLoading(true)
    await onHandleBuyPack()
    setIsLoading(false)
  }

  return (
    <PackageWrapper alignItems="center">
      <ItemWrapper>
        {!isLoadingSaleInfo && (
          <StyledNumberOfSoldPackage>
            {numberOfSoldPacks}/{totalPacks}
          </StyledNumberOfSoldPackage>
        )}
        <CardWrapper src={common.cardBackground} />
        <Video muted autoPlay loop playsInline poster="/images/thumbnail-common-package.png">
          <source src="https://genesis.cyball.com/cyblocpack-media/common.mp4" type="video/mp4" />
        </Video>
        {limitBoughtPackage > 1 && payAmount && (
          <TextWrapper>
            <Flex>
              <StyledChangeValueButton
                color="#F3F3F3"
                fontSize="60px"
                fontWeight="600"
                onClick={() => onChangeValue('down')}
              >
                -
              </StyledChangeValueButton>
              <StyledNumberOfPackage>{numberOfPackage}</StyledNumberOfPackage>
              <StyledChangeValueButton
                color="#F3F3F3"
                fontSize="6rem"
                fontWeight="600"
                onClick={() => {
                  if (limitBoughtPackage !== numberOfPackage) {
                    onChangeValue('up')
                  }
                }}
                disabled={limitBoughtPackage === numberOfPackage}
              >
                +
              </StyledChangeValueButton>
            </Flex>
          </TextWrapper>
        )}
        {limitBoughtPackage > 0 && payAmount && (
          <>
            <StyledButton
              buttonBackgroundColor={common.buttonBackgroundColor}
              buttonDisabled={common.buttonDisabled}
              isLoading={isLoading}
              onClick={() => {
                if (!isLoading) {
                  onBuyPack()
                }
              }}
            >
              {isLoading ? (
                <LoaderIcon />
              ) : (
                <Text color="#110A19" fontWeight="700">
                  Buy for {getFullDisplayBalance(payAmount, 18, 2)} BNB
                </Text>
              )}
            </StyledButton>
          </>
        )}
      </ItemWrapper>
      <ItemInfo cardBackgroundColor={common.cardBackgroundColor}>
        <StyledClassInfoHeader>
          <PreviewText isPreview={isPreview} onClick={() => setIsPreview(true)}>
            Preview
          </PreviewText>
          <StatsText isPreview={isPreview} onClick={() => setIsPreview(false)}>
            Probability
          </StatsText>
        </StyledClassInfoHeader>
        <SwitchTransition mode="out-in">
          <CSSTransition
            key={isPreview}
            addEndListener={(node, done) => {
              node.addEventListener('transitionend', done, false)
            }}
            classNames="fade"
          >
            {isPreview ? <Preview /> : <Stats />}
          </CSSTransition>
        </SwitchTransition>
      </ItemInfo>
    </PackageWrapper>
  )
}

export default PackageCommon
