import React, { useEffect, useMemo, useState } from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import { useModal, CardDetailModal, OpenPackModal } from 'components/Modal'
import { setNewCylocList, setSelectedPackId } from 'state/reducers/cybloc'
import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'
import PageLoading from 'components/PageLoading'
import { useAppDispatch } from 'state'
import { selectContractAddress } from 'state/reducers/app'
import { CommonPackageCard, RarePackageCard, EpicPackageCard } from './components/PackageCard'
import { PreviewCommon, PreviewEpic, PreviewRare } from './components/Preview'

const Wrapper = styled(Box)`
  background-color: #0f0a18;
  min-height: inherit;
`
const Row = styled(Box)`
  max-width: 1000px;
  margin: 0 auto;

  @media (max-width: 1100px) {
    max-width: 800px;
  }

  @media (max-width: 800px) {
    max-width: 80%;
  }

  @media (max-width: 576px) {
    max-width: 350px;
  }
`
const StyledHeaderContent = styled(Flex)`
  justify-content: space-between;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 40px;
  }
`

const StyledHeaderText = styled(Text)`
  font-size: ${(props) => (props.isActive ? '3rem' : '1.4rem')};
  line-height: 48px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #f3f3f3;
  cursor: pointer;
  padding: 30px 0px;
  opacity: ${(props) => (props.isActive ? '1' : '0.6')};
  transition: 0.3s opacity ease-out;

  @media (max-width: 600px) {
    padding: 0px;
  }
`
const Grid = styled(Box)`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 40px;
  row-gap: 40px;

  @media (max-width: 1100px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: 580px) {
    grid-template-columns: 1fr;
  }
`

const Empty = styled(Box)`
  width: 100%;
  display: flex;
  justify-content: center;
`

const Inventory = ({ packageData, isLoading }) => {
  const {
    CYBLOC_PACK: { ADDRESS: PACK_ADDRESS },
  } = useSelector(selectContractAddress)
  const dispatch = useAppDispatch()
  const [selectedPackage, setSelectedPackage] = useState({
    type: null,
    packageIndex: [],
  })
  const [onPresentPreviewCommonPack, _onDimissPreviewCommonPack] = useModal(
    <CardDetailModal>
      <PreviewCommon
        onDismiss={() => {
          _onDimissPreviewCommonPack()
        }}
      />
    </CardDetailModal>,
  )

  const [onPresentPreviewRarePack, _onDimissPreviewRarePack] = useModal(
    <CardDetailModal>
      <PreviewRare
        onDismiss={() => {
          _onDimissPreviewRarePack()
        }}
      />
    </CardDetailModal>,
  )

  const [onPresentPreviewEpicPack, _onDimissPreviewEpicPack] = useModal(
    <CardDetailModal>
      <PreviewEpic
        onDismiss={() => {
          _onDimissPreviewEpicPack()
        }}
      />
    </CardDetailModal>,
  )

  useEffect(() => {
    dispatch(setNewCylocList([]))
  }, [])

  const { commonPackageData, rarePackageData, epicPackageData } = packageData
  return (
    <Wrapper>
      <Row>
        <StyledHeaderContent>
          <Flex alignItems="baseline">
            <StyledHeaderText isActive mr="15px">
              Packs
            </StyledHeaderText>
          </Flex>
        </StyledHeaderContent>
        {isLoading ? (
          <PageLoading />
        ) : commonPackageData.length === 0 && rarePackageData.length === 0 && epicPackageData.length === 0 ? (
          <Empty>
            <StyledHeaderText>No packs to display</StyledHeaderText>
          </Empty>
        ) : (
          <Grid>
            {commonPackageData.map(({ tokenId }) => {
              return (
                <CommonPackageCard
                  data={tokenId}
                  onClick={() => {
                    dispatch(setSelectedPackId(tokenId))
                    onPresentPreviewCommonPack()
                  }}
                  selectedPackage={selectedPackage}
                />
              )
            })}
            {rarePackageData.map(({ tokenId }) => {
              return (
                <RarePackageCard
                  data={tokenId}
                  onClick={() => {
                    dispatch(setSelectedPackId(tokenId))
                    onPresentPreviewRarePack()
                  }}
                  selectedPackage={selectedPackage}
                />
              )
            })}
            {epicPackageData.map(({ tokenId }) => {
              return (
                <EpicPackageCard
                  data={tokenId}
                  onClick={() => {
                    dispatch(setSelectedPackId(tokenId))
                    onPresentPreviewEpicPack()
                  }}
                  selectedPackage={selectedPackage}
                />
              )
            })}
          </Grid>
        )}
      </Row>
    </Wrapper>
  )
}

export default Inventory
