import React from 'react'
import styled from 'styled-components'
import { toast } from 'react-toastify'
import { ConnectorNames, connectorLocalStorageKey } from 'utils/web3React'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Box from 'components/Box'
import useAuth from 'hooks/useAuth'
import useMediaQuery from 'hooks/useMediaQuery'

import Modal from './BaseModal'

const Wrapper = styled(Box)`
  text-align: center;
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;

  @media (max-width: 576px) {
    padding: 75px 0px;
    justify-content: center;
  }
`

const WalletButton = styled(Flex)`
  justify-content: center;
  align-items: flex-start;
  background-image: url('/images/login-button-background.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 13px 0px;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:hover {
    cursor: pointer;
    background-image: url('/images/login-button-background-hover.png');
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`

const StyledModalHeader = styled(Text)`
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
  padding: 0 60px;
  color: #ffffff;
  font-style: normal;
  line-height: 150%;
  color: #f3f3f3;

  @media (max-width: 576px) {
    padding: 0 30px;
  }
  & > a {
    text-decoration: underline;
  }
`

const StyledSubText = styled(Text)`
  font-size: 1rem;
  color: #ffffff;
  line-height: 20px;
  letter-spacing: 2px;
  @media (max-width: 576px) {
    height: 0px;
  }
`

const connectors = [
  {
    title: 'Metamask',
    connectorId: ConnectorNames.Injected,
  },
  {
    title: 'WalletConnect',
    connectorId: ConnectorNames.WalletConnect,
  },
  {
    title: 'Binance Wallet',
    connectorId: ConnectorNames.BSC,
  },
]

const LoginModal = ({ onDismiss }) => {
  const { login } = useAuth()
  const isMobile = useMediaQuery('(max-width: 576px)')
  return (
    <Modal title="Your wallet">
      <Wrapper>
        <Box>
          <StyledModalHeader>Log in by connecting a wallet</StyledModalHeader>
        </Box>
        <Box>
          <WalletButton
            onClick={() => {
              if (window.ethereum && window.ethereum.isMetaMask && window.ethereum.isMetaMask === true) {
                login(connectors[0].connectorId)
                localStorage.setItem(connectorLocalStorageKey, connectors[0].connectorId)
                onDismiss()
              } else {
                toast.error('No MetaMask Wallet', {
                  hideProgressBar: true,
                })
              }
            }}
          >
            <Text mr="5px">Metamask</Text>
          </WalletButton>
          <WalletButton
            onClick={() => {
              login(connectors[1].connectorId)
              localStorage.setItem(connectorLocalStorageKey, connectors[1].connectorId)
              onDismiss()
            }}
          >
            <Text mr="5px">WalletConnect</Text>
          </WalletButton>
          <WalletButton
            onClick={() => {
              if (window.BinanceChain) {
                login(connectors[2].connectorId)
                localStorage.setItem(connectorLocalStorageKey, connectors[2].connectorId)
                onDismiss()
              } else {
                toast.error('No Binance Wallet', {
                  hideProgressBar: true,
                })
              }
            }}
          >
            <Text mr="5px">Binance Wallet</Text>
          </WalletButton>
          <WalletButton
            onClick={() => {
              if (window.ethereum && window.ethereum.isCoin98 && window.ethereum.isCoin98 === true) {
                login(connectors[0].connectorId)
                localStorage.setItem(connectorLocalStorageKey, connectors[0].connectorId)
                onDismiss()
              } else {
                toast.error('No Coin98 Wallet', {
                  hideProgressBar: true,
                })
              }
            }}
          >
            <Text mr="5px">Coin98 Wallet</Text>
          </WalletButton>
        </Box>
        {isMobile === false ? (
          <StyledSubText mt="15px">
            By connecting a wallet, you agree to CyBalls&nbsp;
            <a
              style={{ textDecoration: 'underline', textDecorationColor: '#F3F3F3' }}
              href="https://docs.cyball.com/others/terms-of-use"
              target="_blank"
              rel="noreferrer"
            >
              Terms of Use
            </a>
            &nbsp;and acknowledge that you have read and understood the&nbsp;
            <a
              style={{ textDecoration: 'underline', textDecorationColor: '#F3F3F3' }}
              href="https://docs.cyball.com/others/disclaimer"
              target="_blank"
              rel="noreferrer"
            >
              Disclaimer
            </a>{' '}
            and&nbsp;
            <a
              style={{ textDecoration: 'underline', textDecorationColor: '#F3F3F3' }}
              href="https://docs.cyball.com/others/privacy-policy"
              target="_blank"
              rel="noreferrer"
            >
              Privacy Policy
            </a>
          </StyledSubText>
        ) : (
          <></>
        )}
      </Wrapper>
    </Modal>
  )
}

export default LoginModal
